* {
  -ms-overflow-style: none; 
  scrollbar-width: none; 
}
::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.container {
  margin: auto;
  margin-top: 10%;
  background-color: #219ebc;
  padding: 20px;
  width: 700px;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

h1 {
  color: #000000;
  text-align: center;
  margin-top: 10;
}
.list-items {
  min-height: 150px;
  max-height: 3000px;
  overflow-y: scroll;
}
.list-input {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: 100%;
  position: relative;
}

.list-input input {
  width: 100%;
  height: 7vh;
  border-radius: 40px;
  border: 1px solid;
  padding-left: 15px;
  font-size: 25px;
  outline: none;
  background-color: #8ecae6;
  border-color: #8ecae6;
  color: #000000;
}

.list-input button {
  position: absolute;
  background-color: #023047;
  border-radius: 40px;
  border: 1px solid #023047;
  width: 30px;
  height: 30px;
  font-size: 25px;
  margin-right: 15px;
  font-weight: bolder;
  color: #fefae0;
}

.list-input button:hover {
  background-color: #780000;
}

.list-input button:disabled {
  border: 1px solid #023047;

  background-color: #023047;
}

.element-list {
  display: flex;
  gap: 10px;
  flex-direction: column;
  align-items: stretch;
  padding: 10;
}

@media (max-width: 450px) {
  .container {
    width: 300px;
    padding: 20px;
  }
}