.wrapper {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 10px;
  height: 40px;
  padding: 10px;
  width: 100%;
  background-color: #ffb703;
  color: #000000;
}

.wrapper li {
  list-style: none;
  font-size: 30px;
  position: relative;
  width: 85%;
}

.checker,
.delete {
  height: 30px;
  width: 40px;
  padding: 10;
}

.checker {
  border: 10px #8ecae6 solid;
}

.checker:hover {
  background-color: #023047;
  border-color: #023047;
}
.delete:hover {
  background-color: #780000;
  border-color: #780000;
}

.checker.active {
  background-size: 100% 100%;

  border-radius: 50%;
  border: 1px solid;
}

.delete {
  font-weight: bold;
  color: white;
  background-color: #c1121f;
  border: 1px solid #c1121f;
  border-radius: 100%;
  text-align: center;
}

.element-list li.active {
  text-decoration: line-through;
}

@media (max-width: 450px) {
  .checker,
  .delete {
    height: 30px;
    width: 30px;
    padding: 0;
  }
}